  background-image: none;
}
#toolbar-administration #admin-toolbar-search-tab .form-type-textfield {
  margin: 0;
  padding: 7px 5px 0;
}
#toolbar-administration #admin-toolbar-search-tab #admin-toolbar-search-input {
  color: #fff;
  border: 1px solid #111 !important;
  border-radius: 3px;
  background-color: #111 !important;
  box-shadow: inset 0 -1px 0 0 rgba(255, 255, 255, 0.15);
  height: 26px;
  min-height: 26px;
}

